
<template>
  <div class="map-container">
    <div ref="map" class="map"></div>
  </div>
</template>

<script>
export default {
  name: 'MapComponent',
  data() {
    return {
      map: null,
      mapLoaded: false,
    };
  },
  mounted() {
    this.loadMapScript();
  },
  methods: {
    loadMapScript() {
      if (!this.mapLoaded && !window.google) {
        const script = document.createElement('script');
        script.src = `https://maps.googleapis.com/maps/api/js?key=AIzaSyC3EtWIuDUa-5yV6f13gUdZPJtQS7H2QJA&callback=initMapCallback`;
        script.async = true;
        script.defer = true;
        window['initMapCallback'] = this.initMap;
        document.head.appendChild(script);
      } else if (window.google && !this.map) {
        this.initMap();
      }
    },
    initMap() {
      if (!this.map) {
        this.mapLoaded = true;
        this.map = new window.google.maps.Map(this.$refs.map, {
          center: { lat: 40.7128, lng: -74.0060 },
          zoom: 12,
        });
      }
    },
  },
};
</script>
